    gallery = props => {
        return (
            <InfiniteCarousel
            breakpoints={[
              {
                breakpoint: 500,
                settings: {
                  slidesToShow: 2,
                  slidesToScroll: 2,
                },
              },
              {
                breakpoint: 768,
                settings: {
                  slidesToShow: 3,
                  slidesToScroll: 3,
                },
              },
            ]}
            dots={true}
            showSides={true}
            sidesOpacity={.5}
            sideSize={.7}
            slidesToScroll={1}
            slidesToShow={1}
            scrollOnDevice={true}
          >
            <div>
              <img
                alt=''
                src={resource1}
              />
            </div>
            <div>
              <img
                alt=''
                src={resource2}
              />
            </div>
            <div>
              <img
                alt=''
                src={resource3}
              />
            </div>
            <div>
              <img
                alt=''
                src={resource4}
              />
            </div>
            <div>
              <img
                alt=''
                src={resource5}
              />
            </div>
          </InfiniteCarousel>)
    }
    render() {
        const {classes} = this.props;
        return ( 
            <div className={classes.root}>
                        <FirebaseContext.Consumer>
                            {
                                firebase => <AppBar firebase={firebase}/>
                            }
                        </FirebaseContext.Consumer>

                       {this.headerGraphic(this.props)}
                       {this.foundationsComponent(this.props)}
                       {this.dataComponent(this.props)}
                       {this.measuringComponent(this.props)}
                       {this.gallery(this.props)}
            </div>
        );
    }
